import React, { useEffect, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ReportTable from "./ReportTable/ReportTable";
import { useMutation } from "react-query";
import Select from "react-select";
import {
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CRow,
  CButton,
} from "@coreui/react";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import { ReportData, CheckPointReportData } from "src/hooks/useSensors";
import {
  _department,
  _facility,
  _organization,
  _device,
  _view,
  _create,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";
const userPermissions = JSON.parse(localStorage.getItem("permissions"));

const organizationPermission = hasPermissionToActionAccess(
  userPermissions,
  _organization,
  _view
);
const facilityPermission = hasPermissionToActionAccess(
  userPermissions,
  _facility,
  _view
);
const departmentPermission = hasPermissionToActionAccess(
  userPermissions,
  _department,
  _view
);
const devicepermission = hasPermissionToActionAccess(
  userPermissions,
  _device,
  _view
);
function TableCard() {
  const [startDate, setStartDate] = useState(new Date());
  const [day, setDay] = useState(1);
  const [data, setData] = useState([]);
  const [sensors, setSensors] = useState([]);
  const [intervals, setIntervals] = useState([]);
  const payload = {
    days: day,
    startDate: startDate.toISOString(), // Convert the date to ISO format
  };
  const { darkMode, setDarkMode } = useGlobalInfo();
  const { mutate: reportData } = useMutation(ReportData);
  const { mutate: checkpointReport } = useMutation(CheckPointReportData);
  function reportDataFetch(selectedId) {
    reportData(selectedId, {
      onSuccess: (data) => {
        // console.log(data);
      },
      onError: (error) => {},
    });
  }
  function checkpointReportDataFetch(payload, id) {
    const combinedData = {
      payload,
      id,
    };

    checkpointReport(combinedData, {
      onSuccess: (data) => {
        debugger;
        setData(data.data.checkPoints);
        setIntervals(data.data.intervals);
        setSensors(data.data.sensors);
      },
      onError: (error) => {},
    });
  }

  useEffect(() => {
    const fetchData = () => {
      reportDataFetch();
    };
    fetchData();

    const intervalId = setInterval(fetchData, 300000);

    return () => clearInterval(intervalId);
  }, []);
  useEffect(() => {
    const payload = {
      days: day,
      startDate: startDate.toISOString(),
      sensorIds: [],
    };
    if (organizationPermission) {
      debugger;
      checkpointReportDataFetch(
        payload,
        localStorage.getItem("OrganizationId")
      );
    } else {
      checkpointReportDataFetch(
        payload,
        localStorage.getItem("organizationid")
      );
    }
  }, [startDate, day]);
  const handleDatePickerChange = (date) => {
    setStartDate(date);
    const payload = {
      days: day,
      startDate: date.toISOString(),
    };

    checkpointReportDataFetch(payload);
  };
  const handleSelectChange = (e) => {
    const selectedDay = Number(e.target.value);
    setDay(selectedDay);
    const payload = {
      days: selectedDay,
      startDate: startDate.toISOString(),
    };

    checkpointReportDataFetch(payload);
  };
  const [selectedSensors, setSelectedSensors] = useState([]);
  const handleMultiSelectChange = (selectedOptions) => {
    // Store an array of sensor type IDs in the selectedSensors state
    const selectedSensorTypeIds = selectedOptions.map((sensor) => sensor.value);
    setSelectedSensors(selectedSensorTypeIds);
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    const payload = {
      days: day,
      startDate: startDate.toISOString(),
      sensorIds: selectedSensors,
    };

    if (organizationPermission) {
      checkpointReportDataFetch(
        payload,
        localStorage.getItem("OrganizationId")
      );
    } else {
      checkpointReportDataFetch(
        payload,
        localStorage.getItem("organizationid")
      );
    }
  };
  return (
    <CCard
      className={`table_container_light ${
        darkMode ? "table_container_dark" : ""
      }`}
      style={{
        marginTop: "2rem",
        borderRadius: "5px",
        padding: "25px",
        border: "1px solid rgba(163, 163, 163, 0.32)",
      }}
    >
      <h3 id={`${darkMode ? "heading-dark" : ""}`}>CheckPoint Report</h3>
      <form className="">
        <div className="d-flex gap-5 mt-2 ">
          <div className="Custom-Date items-center d-flex gap-2 w-50">
            <label
              htmlFor="datePicker"
              id={`${darkMode ? "heading-dark" : ""}`}
            >
              Select a Date
            </label>
            <DatePicker
              className={` form-select ${darkMode ? "select-dark" : ""}`}
              selected={startDate}
              name="startDate"
              dateFormat="MM/dd/yyyy"
              placeholderText="Select a date"
              onChange={handleDatePickerChange}
            />
          </div>
          <div className="d-flex">
            <label
              htmlFor="datePicker"
              id={`${darkMode ? "heading-dark" : ""}`}
              style={{ marginRight: "20px", marginLeft: "30px" }}
            >
              Select Sensors
            </label>
            <Select
              isMulti
              className={`form-select ${darkMode ? "select-dark" : ""}`}
              value={selectedSensors.map((sensorTypeId) => ({
                label: sensors.find(
                  (sensor) => sensor.sensorTypeId === sensorTypeId
                )?.sensorValue,
                value: sensorTypeId,
              }))}
              options={sensors.map((sensor) => ({
                label: sensor.sensorValue,
                value: sensor.sensorTypeId,
              }))}
              onChange={handleMultiSelectChange}
              styles={{
                container: (provided) => ({
                  ...provided,
                  width: 300,
                }),
              }}
            />

            <button
              onClick={handleSubmit}
              style={{
                width: "100px",
                height: "45px",
                borderColor: "lightgray",
                borderBottomColor: "lightgray",
                borderRadius: "5px",
                boxShadow: "3px 3px  0 rgba(212, 211, 209, 0.9) ",
                marginLeft: "10px",
                marginTop: "0px",
              }}
            >
              Submit
            </button>

            <div className="d-flex" style={{ marginRight: "0px" }}>
              <label
                htmlFor="datePicker"
                id={`${darkMode ? "heading-dark" : ""}`}
                style={{ marginRight: "20px", marginLeft: "30px" }}
              >
                Days
              </label>
              <select
                className={`form-select ${darkMode ? "select-dark" : ""}`}
                value={day}
                style={{ width: "80px" }}
                onChange={handleSelectChange}
              >
                {[1, 2, 3, 4, 5, 6, 7].map((value) => (
                  <option key={value} value={value}>
                    {value}
                  </option>
                ))}
              </select>
            </div>
            <div className="d-flex gap-2" style={{ marginLeft: "5px" }}>
              {Object.entries(intervals).map(
                ([intervalName, intervalValue]) => (
                  <button
                    key={intervalValue}
                    color="primary"
                    className="float-end mr-3"
                    style={{
                      width: "50px",
                      height: "45px",
                      borderColor: "lightgray",
                      borderBottomColor: "lightgray",
                      borderRadius: "5px",
                      boxShadow: "3px 3px  0 rgba(212, 211, 209, 0.9) ",
                    }}
                    onClick={(e) => e.preventDefault()}
                  >
                    {intervalValue}
                  </button>
                )
              )}
            </div>
          </div>
        </div>
      </form>

      <div className="overflow-x-scroll w-100">
        <ReportTable date={startDate} day={day} data={data} />
      </div>
    </CCard>
  );
}

export default TableCard;
