import React, { useState, useEffect } from "react";
import AddSensorConfigration from "../add-sensor-configration/add-sensor-configration";
import { GenericModal } from "src/components/modal/GenericModal";
import PropTypes from "prop-types";
import {
  CButton,
  CCol,
  CForm,
  CFormCheck,
  CFormSelect,
  CFormInput,
  CFormLabel,
  CInputGroup,
  CInputGroupText,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import {
  cibMailchimp,
  cil3d,
  cil4k,
  cilActionUndo,
  cilAirplay,
  cilBuilding,
  cilEnvelopeClosed,
  cilLibraryBuilding,
  cilLocationPin,
  cilPaperPlane,
  cilPaperclip,
  cilPhone,
  cilTerminal,
} from "@coreui/icons";
import {
  _department,
  _facility,
  _organization,
  _device,
  _view,
  _create,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";
import { useLoader } from "src/global-context/LoaderContext";
import { getAllSensors } from "src/hooks/useOrganizationDevices";
import { getAssignedOrg } from "src/hooks/useDevicesSensors";
import {
  getAllDevicesData,
  useAllDevicesData,
  addDevice,
  EditDevice,
  deleteDevice,
  getAllDevicesById,
  getAllDevicesByFacId,
  getAllDevicesByOrgId,
} from "src/hooks/useDevices";
import { useNavigate } from "react-router-dom";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import { useMutation } from "react-query";
const AddDeviceSensor = ({ closeModal, saveHandler, data }) => {
  const { dispatch } = useLoader();
  const { data: allDevices } = useAllDevicesData();
  const navigate = useNavigate();
  const [deviceData, setDevicesData] = useState([]);
  const showLoader = () => dispatch({ type: "SHOW_LOADER" });
  const userPermissions = JSON.parse(localStorage.getItem("permissions"));
  const organizationPermission = hasPermissionToActionAccess(
    userPermissions,
    _organization,
    _view
  );
  const facilityPermission = hasPermissionToActionAccess(
    userPermissions,
    _facility,
    _view
  );
  const departmentPermission = hasPermissionToActionAccess(
    userPermissions,
    _department,
    _view
  );
  const devicepermission = hasPermissionToActionAccess(
    userPermissions,
    _device,
    _view
  );
  const [AddsensorConfiguration, setIsAddsensorConfiguration] = useState(false);
  const [validated, setValidated] = useState(false);
  const orgID = localStorage.getItem("OrganizationId");
  const organizationId = localStorage.getItem("organizationid");
  // const [orgID , setorgID] = useState()
  const [deviceId, setDeviceId] = useState();
  const [formData, setFormData] = useState({
    aws_sensorid: "",
    sensorname: "",
    deviceid: deviceId,
    customerid: Number(orgID),
    // active: true,
  });
  useEffect(() => {
    //debugger
    if (data) {
      setFormData({
        customerid: data.customerid,
        aws_sensorid: data.aws_sensorid,
        sensorname: data.sensorname,
        deviceid: data.deviceid,
        // active: true,
      });
    }
  }, [data]);
  const { mutate: getAllSensorsData, data: sensorData } =
    useMutation(getAssignedOrg);
  // const { mutate: getAllSensorsOrgData, data: sensorData } = useMutation(getAllSensorsDataofOrg)
  const { mutate: getDevices } = useMutation(getAllDevicesData);
  function getAllDevices() {
    getDevices("", {
      onSuccess: (data) => {
        setDevicesData(data);
      },
      onError: (error) => {},
    });
  }
  const { mutate: getDevicesByID } = useMutation(getAllDevicesById);
  function devicesDataFetch(selectedId) {
    // showLoader();
    getDevicesByID(selectedId, {
      onSuccess: (data) => {
        // hideLoader();
        setDevicesData(data);
      },
      onError: (error) => {
        // hideLoader();
      },
    });
  }
  const { mutate: getDevicesByOrgID } = useMutation(getAllDevicesByOrgId);
  function devicesDataByOrg(selectedId) {
    // showLoader();
    getDevicesByOrgID(selectedId, {
      onSuccess: (data) => {
        // hideLoader();
        setDevicesData(data);
      },
      onError: (error) => {
        // hideLoader();
      },
    });
  }
  const { mutate: getDevicesByFacID } = useMutation(getAllDevicesByFacId);
  function devicesDataByFac(selectedId) {
    // showLoader();
    getDevicesByFacID(selectedId, {
      onSuccess: (data) => {
        // hideLoader();
        setDevicesData(data);
      },
      onError: (error) => {
        // hideLoader();
      },
    });
  }
  useEffect(() => {
    if (organizationPermission) {
      // debugger;
      getAllSensorsData(orgID, {
        onSuccess: (data) => {
          //debugger
        },
        onError: (error) => {},
      });
    } else if (!organizationPermission) {
      debugger;
      getAllSensorsData(organizationId, {
        onSuccess: (data) => {
          //debugger
        },
        onError: (error) => {},
      });
    }
  }, []);
  useEffect(() => {
    const organizationId = localStorage.getItem("OrganizationId");
    const facilityId = localStorage.getItem("facilityId");
    const departmentId = localStorage.getItem("departmentId");

    if (departmentId && departmentId !== "Select Department") {
      // Only departmentId is selected
      devicesDataFetch(departmentId);
    } else if (facilityId && facilityId !== "Select Facility") {
      // facilityId is selected, prioritize it over organizationId
      devicesDataByFac(facilityId);
    } else if (organizationId && organizationId !== "Select Organization") {
      // Only organizationId is selected and it's not "Select Organization"
      devicesDataByOrg(organizationId);
    } else {
      // Fallback: getAllDevices
      getAllDevices();
    }
  }, [
    localStorage.getItem("OrganizationId"),
    localStorage.getItem("facilityId"),
    localStorage.getItem("departmentId"),
  ]);

  const sensorConfigure = () => {
    navigate("/devices-sensors/sensor-configration");
  };
  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    const phoneNumber = formData.organizationContact;
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();
    }

    setValidated(true);

    // Handle form submission here
    if (form.checkValidity() === true) {
      showLoader();
      saveHandler(formData);
      event.preventDefault();
      // sensorConfigure()
      closeModal();
    }
  };
  const handledeviceChange = (e) => {
    const selectedDeviceId = parseInt(e.target.value, 10) || 0; // Parse as an integer or default to 0
    setDeviceId(selectedDeviceId);
    setFormData({
      ...formData,
      deviceid: selectedDeviceId,
    });
  };
  const { darkMode, setDarkMode } = useGlobalInfo();
  return (
    <CForm
      className="row mt-2 g-3 needs-validation"
      noValidate
      validated={validated}
      onSubmit={handleSubmit}
    >
      {/* Sensor Id */}
      {/* <CCol md={12}>
        <CFormLabel id={`${darkMode ? 'heading-dark' : ''}`} htmlFor="validationSensorId">
          Sensor Id *
        </CFormLabel>
        <CInputGroup>
          <CInputGroupText>
            <CIcon icon={cilBuilding} alt="typeSensorid" />
          </CInputGroupText>
          <CFormInput
            type="text"
            className={`input-light pt-0 ${darkMode ? 'input-dark' : ''}`}
            name="sensorId"
            value={formData.sensorId}
            onChange={handleInputChange}
            feedbackInvalid=" Sensor Id is required"
            id="validationSensorId"
            disabled={data ? true : false}
            required
          />
        </CInputGroup>
      </CCol> */}
      <CCol md={12}>
        <CFormLabel
          id={`${darkMode ? "heading-dark" : ""}`}
          htmlFor="validationSensorId"
        >
          Sensor Id*
        </CFormLabel>
        <CFormSelect
          aria-describedby="validationCustom04Feedback"
          feedbackInvalid="Please select a valid sensor id"
          name="aws_sensorid"
          className={` form-select ${darkMode ? "select-dark" : ""}`}
          value={formData.aws_sensorid}
          id="validationSensorId"
          onChange={handleInputChange}
          disabled={data ? true : false}
        >
          <option>Select Sensors</option>
          {sensorData?.data?.map((item, index) => {
            return (
              <option value={item.aws_sensorid} key={index}>
                {item.aws_sensorid}
              </option>
            );
          })}
        </CFormSelect>
      </CCol>
      {/*Sensor name*/}
      <CCol md={12}>
        <CFormLabel
          id={`${darkMode ? "heading-dark" : ""}`}
          htmlFor="validationSensorname"
        >
          Sensor Name*
        </CFormLabel>
        <CInputGroup>
          <CInputGroupText>
            <CIcon icon={cilBuilding} alt="typeSensorid" />
          </CInputGroupText>
          <CFormInput
            className={`input-light pt-0 ${darkMode ? "input-dark" : ""}`}
            type="text"
            name="sensorname"
            value={formData.sensorname}
            onChange={handleInputChange}
            feedbackInvalid=" Sensor Name is required"
            id="validationSensorname"
            required
          />
        </CInputGroup>
      </CCol>
      {/*Device Selection */}
      <CCol md={12}>
        {/* Form select*/}
        <CFormLabel
          id={`${darkMode ? "heading-dark" : ""}`}
          htmlFor="validationDeviceSelection"
        >
          Equipment Selection*
        </CFormLabel>
        <CFormSelect
          className={` form-select ${darkMode ? "select-dark" : ""}`}
          aria-describedby="validationCustom04Feedback"
          name="deviceSelection"
          value={deviceId}
          feedbackInvalid="Please select a valid Device Selection"
          id="validationDeviceSelection"
          onChange={handledeviceChange}
          required
        >
          <option>Select Equipment</option>
          {deviceData?.data?.map((item) => {
            return (
              <option key={item.deviceid} value={item.deviceid}>
                {item.devicename}
              </option>
            );
          })}
        </CFormSelect>
      </CCol>

      {/* Submit Button */}
      <CCol xs={12}>
        <CButton color="primary" type="submit">
          {data ? "Edit Device Sensor" : "Add Device Sensor"}
        </CButton>
      </CCol>
    </CForm>
  );
};
AddDeviceSensor.propTypes = {
  closeModal: PropTypes.func.isRequired,
  saveHandler: PropTypes.func.isRequired,
  // TableHide: PropTypes.func.isRequired,
  data: PropTypes.shape({
    sensorId: PropTypes.string,
    aws_sensorid: PropTypes.string,
    sensorname: PropTypes.string,
    deviceid: PropTypes.number,
    customerid: PropTypes.number,
    active: PropTypes.bool,
  }),
};
export default AddDeviceSensor;
