import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CRow,
} from "@coreui/react";
import Skeleton from "react-loading-skeleton";
import Battery from "../../../assets/images/battery.svg";
import {
  _department,
  _facility,
  _organization,
  _device,
  _view,
  _create,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";
// import { getSensorsData } from "services/sensor";
import { useNavigate } from "react-router-dom";
// import { getTimeDifference } from "variables/DonutData";
// import Gauge from "./Gauge";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import { useMutation } from "react-query";
import Gauge from "./Guage";
import { getAllSensorData , SensorGraphData } from "src/hooks/useSensors";
import PropTypes from "prop-types";

import "./Guage.css";
const Donut = ({
  setAlarms,
  setoffline,
  setOk,
  setwarnings,
  showAlarms,
  showoffline,
  showok,
  showwarnings,
}) => {
  const dummySensorData = [
    {
      sensorId: "1",
      sensorName: "Heater 1",
      minValue: 0,
      maxValue: 100,
      value: 75,
      readingDateTime: new Date(),
      batteryValue: 3.8,
    },
    {
      sensorId: "2",
      sensorName: "Temp 2",
      minValue: 0,
      maxValue: 100,
      value: 50,
      readingDateTime: new Date(),
      batteryValue: 3.2,
    },
    // Add more dummy data as needed
  ];
  const { mutate: SensorsData } = useMutation(getAllSensorData);
  const { mutate: SensorGraph } = useMutation(SensorGraphData);

  const [widgetsData, setWidgetsData] = useState();
  const [sensorData, setSensorData] = useState([]);
  const [loadingDonut, setLoadingDonut] = useState(false);
  const userPermissions = JSON.parse(localStorage.getItem("permissions"));
  const organizationPermission = hasPermissionToActionAccess(
    userPermissions,
    _organization,
    _view
  );
  const facilityPermission = hasPermissionToActionAccess(
    userPermissions,
    _facility,
    _view
  );
  const departmentPermission = hasPermissionToActionAccess(
    userPermissions,
    _department,
    _view
  );
  const devicepermission = hasPermissionToActionAccess(
    userPermissions,
    _device,
    _view
  );
  function getAllSensorsData(id) {
    SensorsData(id, {
      onSuccess: (data) => {
        setWidgetsData(data.data);
        setSensorData(data.data); // Set sensorData here
      },
      onError: (error) => {
        // Handle error if needed
      },
    });
  }
  function getSensorGraphData(payload) {
    debugger
    SensorGraph(payload, {
      onSuccess: (data) => {
        navi(`/sensor/graph`, { state: { sensorGraphData: data.data } });
        // Set sensorData here
      },
      onError: (error) => {
        // Handle error if needed
      },
    });
  }
  const handleCardClick = (item) => {
    debugger
    const currentDate = new Date();
    const formattedDate = currentDate.toISOString(); // Format: 2023-12-21T00:00:00.000Z
  
    const payload = {
      sensorTypeId: item.sensorTypeId,
      awsSensorId: item.awsSensorId,
      startDate: formattedDate,
      endDate: formattedDate,
    };
  
    getSensorGraphData(payload);
    // navi(`/sensor/graph`);
  };
  const organizationId = localStorage.getItem("OrganizationId");
  useEffect(() => {
    const fetchData = () => {
      if (organizationPermission) {
        const orgId = localStorage.getItem("OrganizationId");
        if (
          orgId !== null &&
          orgId !== undefined &&
          orgId !== "Select Organization"
        ) {
          setLoadingDonut(true);
          getAllSensorsData(orgId);
          setLoadingDonut(false);
        }
      } else {
        const orgId = localStorage.getItem("organizationid");
        if (orgId !== null) {
          setLoadingDonut(true);
          getAllSensorsData(orgId);
          setLoadingDonut(false);
        }
      }
    };
  
    fetchData();
  
    const intervalId = setInterval(fetchData, 300000); 
  
    return () => clearInterval(intervalId); 
  }, [organizationPermission]); 
  
  let newWarnings = 0;
  let newOffline = 0;
  let newOk = 0;
  let warningSensors = [];
  let offlineSensors = [];
  let okSensors = [];
  sensorData?.forEach((item) => {
    if (
      item?.value !== null &&
      (item?.value < item?.minValue || item?.value > item?.maxValue)
    ) {
      newWarnings++;
      warningSensors.push(item);
    } else if (item?.value === null) {
      newOffline++;
      offlineSensors.push(item);
    } else {
      newOk++;
      okSensors.push(item);
    }
  });

  setwarnings(newWarnings);
  setoffline(newOffline);
  setOk(newOk);
  let sensorsToMap = sensorData;
  if (showwarnings) {
    sensorsToMap = warningSensors;
  }
  else if (showoffline){
    sensorsToMap = offlineSensors
  }
  else if (showok){
    sensorsToMap = okSensors
  } else {
    sensorsToMap = sensorData
  }
  const navi = useNavigate();
  const { darkMode, setDarkMode } = useGlobalInfo();
  return (
    <div className="card-top" style={{ width: "100vw" }}>
      <CRow className="sensor_Container my-5 justify-content-start">
        {loadingDonut ? (
          <>
            {[...Array(2)].map((_, index) => (
              <CCol
                key={index}
                sm="12"
                md="6"
                lg="3"
                className={` ${
                  darkMode ? "table_container_dark" : "table_container_light"
                }`}
              >
                <CCard>
                  <CCardBody>
                    <Skeleton active paragraph={{ rows: 3 }} />
                  </CCardBody>
                </CCard>
              </CCol>
            ))}
            <div></div>
          </>
        ) : (
          sensorsToMap?.map((item, index) => (
            <CCol
              key={index}
              sm="12"
              className={` ${
                darkMode ? "table_container_dark" : "table_container_light"
              }`}
              style={{
                border: "1px solid rgba(163, 163, 163, 0.32)",
                borderRadius: "5px",
                padding: "0",
                width: "auto",
                height: "100%",
              }}
            >
              <CCard
                style={{cursor:'pointer'}}
                className={` ${
                  darkMode ? "table_container_dark" : "table_container_light"
                }`}
                onClick={() => handleCardClick(item)}
              >
                <CCardHeader
                  className="d-flex justify-content-between p-2 align-items-center"
                  id={`${darkMode ? "heading-dark" : ""}`}
                  style={{ height: "45px" }}
                >
                  <p className="m-0">
                    {item?.sensorName ? item.sensorName : item.sensorId}
                  </p>
                  <CCol className="battery">
                    <img
                      src={Battery}
                      alt="Battery"
                      style={{
                        width: "20px",
                        height: "20px",
                        marginLeft: "5px",
                      }}
                    />
                    <div
                      style={{
                        color: item.batteryValue < 3.5 ? "red" : "green",
                      }}
                      id={`${darkMode ? "heading-dark" : ""}`}
                    >
                      {item.batteryValue}
                    </div>
                  </CCol>
                </CCardHeader>
                <CCardBody
                  style={{
                    backgroundColor:
                      item?.value === null
                        ? "rgb(157, 223, 228)"
                        : item?.value < item?.minValue ||
                          item?.value > item?.maxValue
                        ? "rgb(238, 141, 141)"
                        : "rgb(144, 238, 144 )",
                  }}
                >
                  <Gauge
                    devicename={item?.deviceName}
                    awsId={item?.awsSensorId}
                    id={`${darkMode ? "heading-dark" : ""}`}
                    minValue={item?.minValue}
                    maxValue={item?.maxValue}
                    currentValue={isNaN(item?.value) ? 0 : item?.value}
                    readingTime={
                      item?.value !== null ? item?.readingDateTime : ""
                    }
                  />
                </CCardBody>

                {/* <CCardFooter>
                  <CRow>
                    <CCol className="min">
                      <div style={{ display: "flex", gap: "3px" }}>
                        <div
                          className="min-heading"
                          id={`${darkMode ? "heading-dark" : ""}`}
                        >
                          Min
                        </div>
                        <div id={`${darkMode ? "heading-dark" : ""}`}>
                          {item.minValue}
                        </div>
                      </div>
                    </CCol>
                    <CCol className="max">
                      <div
                        style={{
                          display: "flex",
                          gap: "3px",
                          justifyContent: "flex-end",
                          fontSize: "14px",
                        }}
                      >
                        <div
                          className="max-heading"
                          id={`${darkMode ? "heading-dark" : ""}`}
                        >
                          Max
                        </div>
                        <div id={`${darkMode ? "heading-dark" : ""}`}>
                          {item.maxValue}
                        </div>
                      </div>
                    </CCol>
                  </CRow>
                  <Divider />
                  <CRow>
                    <CCol className="hours">
                      {getTimeDifference(new Date(), item?.readingDateTime)}
                    </CCol>
                  </CRow>
                </CCardFooter> */}
              </CCard>
            </CCol>
          ))
        )}
      </CRow>
    </div>
  );
};

export default Donut;

Donut.propTypes = {
  setAlarms: PropTypes.func.isRequired,
  setoffline: PropTypes.func.isRequired,
  setOk: PropTypes.func.isRequired,
  setwarnings: PropTypes.func.isRequired,
  showAlarms: PropTypes.bool.isRequired,
  showoffline: PropTypes.bool.isRequired,
  showok: PropTypes.bool.isRequired,
  showwarnings: PropTypes.bool.isRequired,
};
