import React from "react";
import PropTypes from "prop-types";
import GaugeChart from "react-gauge-chart";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import "./Guage.css";
// import { calculatePercentage } from "variables/DonutData";
const Gauge = ({
  minValue,
  maxValue,
  currentValue,
  readingTime,
  awsId,
  devicename,
}) => {
  const safeCurrentValue = Math.min(Math.max(currentValue, minValue), maxValue);
  const percentage =
    currentValue === null
      ? 0
      : (currentValue - minValue) / (maxValue - minValue);
  const { darkMode, setDarkMode } = useGlobalInfo();

  function calculateTimeDifference(readingDateTime) {
    debugger;
    if (currentValue == null) {
      return <span style={{ color: "red" }}>Offline</span>;
    }

    const currentDateTime = new Date();
    const readingTime = new Date(readingDateTime);

    const timeDifferenceMinutes = Math.floor(
      (currentDateTime - readingTime) / (60 * 1000)
    );

    if (timeDifferenceMinutes < 60) {
      return (
        <span
          style={{ color: "red" }}
        >{`${timeDifferenceMinutes} minutes ago`}</span>
      );
    } else {
      const timeDifferenceHours = Math.floor(timeDifferenceMinutes / 60);
      return (
        <span
          style={{ color: "red" }}
        >{`${timeDifferenceHours} hours ago`}</span>
      );
    }
  }

  function getTimeDifference(currentTime, prevTime) {
    const standardizedReadingDateString =
      prevTime.slice(0, 23).replace(" ", "T") + "Z";
    prevTime = new Date(standardizedReadingDateString);
    currentTime = new Date(currentTime); // Convert to Date object

    const prevTimeValid = !isNaN(prevTime.getTime());
    const currentTimeValid = !isNaN(currentTime.getTime());

    if (!prevTimeValid || !currentTimeValid) {
      return <span style={{ color: "red" }}>Offline</span>;
    }

    const timeDifference = currentTime - prevTime;
    const seconds = Math.floor(timeDifference / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);

    const timeAgo = (value, unit) =>
      `${value} ${unit}${value > 1 ? "s" : ""} ago`;

    if (days > 0) {
      return <span style={{ color: "red" }}>{timeAgo(days, "day")}</span>;
    } else if (hours > 0) {
      return <span style={{ color: "red" }}>{timeAgo(hours, "hour")}</span>;
    } else if (minutes > 0) {
      return <span style={{ color: "red" }}>{timeAgo(minutes, "minute")}</span>;
    } else {
      return <span style={{ color: "red" }}>{timeAgo(seconds, "second")}</span>;
    }
  }

  return (
    <div
      style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <div style={{ marginRight: "00px" }}>
        {devicename}-{awsId}{" "}
      </div>
      <div></div>
      <div>
        <GaugeChart
          id="gauge-chart2"
          nrOfLevels={20}
          colors={["#2ECC71", "#ca0035"]}
          arcWidth={0.3}
          percent={percentage}
          needleColor="#BFC9CA"
          animate={true}
          animDelay={1000}
          animateDuration={2000}
          style={{ height: "auto", width: "auto" }}
        />
      </div>
      <div className="text">
        <div
          className="Min-Max  d-flex"
          style={{ gap: "28px" }}
          id={`${darkMode ? "heading-dark" : ""}`}
        >
          <span>
            {minValue} <span>&#8451;</span>
          </span>
          <span>
            {currentValue} <span>&#8451;</span>
          </span>
          <span>
            {maxValue} <span>&#8451;</span>
          </span>
        </div>
        <div
          style={{
            marginTop: "3px",
            color: isNaN(currentValue) ? "red" : "black",
          }}
        >
          {/* {calculateTimeDifference(readingTime)} */}
          {getTimeDifference(new Date(), readingTime)}
        </div>
      </div>
    </div>
  );
};

export default Gauge;
Gauge.propTypes = {
  minValue: PropTypes.number.isRequired,
  maxValue: PropTypes.number.isRequired,
  currentValue: PropTypes.number.isRequired,
  readingTime: PropTypes.number.isRequired,
  awsId: PropTypes.number.isRequired,
  devicename: PropTypes.number.isRequired,
};
