const AppSidebar = () => {
  const dispatch = useDispatch();
  const unfoldable = useSelector((state) => state.sidebarUnfoldable);
  const sidebarShow = useSelector((state) => state.sidebarShow);
  const { darkMode, setDarkMode } = useGlobalInfo();
  const { mutate: permissions } = useMutation(generalPermissions);
  const filteredNav = FilteredNav();

  useEffect(() => {
    permissions("", {
      onSuccess: (data) => {
        localStorage.setItem("permissions", JSON.stringify(data.permissions));
      },
      onError: (error) => {
        console.log(error);
      },
    });
  }, []);
  const userPermissions = JSON.parse(localStorage.getItem("permissions"));
  const organizationPermission = hasPermissionToActionAccess(
    userPermissions,
    _organization,
    _view
  );
  const facilityPermission = hasPermissionToActionAccess(
    userPermissions,
    _facility,
    _view
  );
  const departmentPermission = hasPermissionToActionAccess(
    userPermissions,
    _department,
    _view
  );
  const devicepermission = hasPermissionToActionAccess(
    userPermissions,
    _device,
    _view
  );
  

  // Use the FilteredNav component to get the filtered navigation items

  return (
    <CSidebar
      position="fixed"
      unfoldable={unfoldable}
      visible={sidebarShow}
      onVisibleChange={(visible) => {
        dispatch({ type: "set", sidebarShow: visible });
      }}
      className={`sidebar-light ${darkMode ? "sidebar-dark" : ""}`}
    >
      <CSidebarBrand
        className={`d-none d-md-flex sidebar-light ${
          darkMode ? "sidebar-dark" : ""
        }`}
        to="/"
      >
    <div style={{ fontSize: "25px" }}>
    {facilityPermission && !organizationPermission && (
  <span>
    {localStorage.getItem("organizationName")}
  </span>
)}

</div>
        {/* Your existing CSidebarBrand content... */}
      </CSidebarBrand>
      <CSidebarNav>
        <SimpleBar>
          {/* Use the filteredNav instead of importing it directly */}
          <AppSidebarNav items={filteredNav} />
        </SimpleBar>
      </CSidebarNav>
      {/* Your existing CSidebarToggler... */}
    </CSidebar>
  );
};

