// /* eslint-disable prettier/prettier */
import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CContainer,
  CHeader,
  CHeaderBrand,
  CHeaderNav,
  CHeaderToggler,
  CNavItem,
  CFormSelect,
  CButton,
  CFormInput,
  CForm,
  CFormLabel,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { useLocation } from "react-router-dom";
import { cilMenu } from "@coreui/icons";
import { GenericModal } from "src/components/modal/GenericModal";
import { AppHeaderDropdown } from "./header/index";
import { logo } from "src/assets/brand/logo";
import { useMutation } from "react-query";
import {
  getDepartmentsData,
  getFacilitiesData,
  getOrganizationData,
  getAllDepByOrgId,
  LogoUpload,
  LogoUploadPatch,
} from "src/hooks/useAuth";
import { getAllFacilitiesData } from "src/hooks/useFacilities";
import { getAllDepartmentsData } from "src/hooks/useDepartments";
import { useLoader } from "src/global-context/LoaderContext";
import { useNavigate } from "react-router-dom";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import useStore from "src/store/state";
import useDataStore from "src/store/state";
import {
  getAllDevicesById,
  getAllDevicesByFacId,
  getAllDevicesByOrgId,
} from "src/hooks/useDevices";
import {
  _department,
  _facility,
  _organization,
  _view,
  _create,
  _device,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";
import Sitelogo from "./../assets/images/avatars/Logo-removebg-preview.png";
import Settings from "src/views/forms/settings-form/Settings";

const AppHeader = () => {
  const location = useLocation();
  const addData = useDataStore((state) => state.addData);
  const userPermissions = JSON.parse(localStorage.getItem("permissions"));
  const { setDepartmentId } = useGlobalInfo();
  const { dispatch } = useLoader();
  const navigate = useNavigate();
  const showLoader = () => dispatch({ type: "SHOW_LOADER" });
  const hideLoader = () => dispatch({ type: "HIDE_LOADER" });
  const dispatched = useDispatch();
  const [organizationData, setOrganizationData] = useState([]);
  const { mutate: facility } = useMutation(getAllFacilitiesData);
  const { mutate: getAllDep } = useMutation(getAllDepartmentsData);
  const organizationPermission = hasPermissionToActionAccess(
    userPermissions,
    _organization,
    _view
  );
  const facilityPermission = hasPermissionToActionAccess(
    userPermissions,
    _facility,
    _view
  );
  const departmentPermission = hasPermissionToActionAccess(
    userPermissions,
    _department,
    _view
  );
  const devicePermission = hasPermissionToActionAccess(
    userPermissions,
    _device,
    _view
  );
  const mail = localStorage.getItem("email");
  let role = "";
  if (organizationPermission) {
    role = "SuperAdmin";
  } else if (facilityPermission) {
    role = "Organization Admin";
  } else if (departmentPermission) {
    role = "Facility Admin";
  } else if (devicePermission) {
    role = "Department Admin";
  }
  const {
    facilityData,
    setFacilityData,
    departmentsData,
    setDepartmentsData,
    devicesData,

    setDevicesData,
  } = useGlobalInfo();
  const handleOrganizationChange = (event) => {
    setDepartmentId("");
    localStorage.removeItem("facilityId");
    localStorage.removeItem("departmentId");

    const selectedId = event.target.value;
    setFacilityData([]);
    setDepartmentsData([]);
    localStorage.setItem("OrganizationId", selectedId);
    // Check if the selected value is "Select Organization"
    if (selectedId !== "Select Organization") {
      facilitiesDataFetch(selectedId);
      departmentsDataByOrg(selectedId);
      devicesDataFetchOrg(selectedId);
    } else {
      // If selected value is "Select Organization", you may want to handle this case
      // For example, clear the organization-specific data or perform other actions
    }
  };

  const handleFacilityChange = (event) => {
    const selectedId = event.target.value;
    localStorage.setItem("facilityId", selectedId);
    localStorage.removeItem("departmentId");
    setDepartmentsData([]);

    // Check if the selected value is "Select Facility"
    if (selectedId !== "Select Facility") {
      departmentsDataFetch(selectedId);
      devicesDataFetchFac(selectedId);
    } else {
      // If selected value is "Select Facility", you may want to handle this case
      // For example, clear the facility-specific data or perform other actions
    }
  };

  const handleDepartmentChange = (event) => {
    const selectedId = event.target.value;

    if (selectedId !== "Select Department") {
      devicesDataFetch(selectedId);
    }

    localStorage.setItem("departmentId", selectedId);
    setDepartmentId(selectedId);
  };

  const sidebarShow = useSelector((state) => state.sidebarShow);
  const { mutate: getOrganization } = useMutation(getOrganizationData);
  function organizationDataFetch() {
    getOrganization("", {
      onSuccess: (data) => {
        hideLoader();
        setOrganizationData(data);
        addData(data.data);
      },
      onError: (error) => {
        if (error.code === "ERR_BAD_REQUEST") {
          localStorage.removeItem("token");
          localStorage.removeItem("OrganizationId");
          localStorage.removeItem("facilityId");
          localStorage.removeItem("departmentId");
          navigate("/login");
        }
        hideLoader();
      },
    });
  }
  function getAllFacilities() {
    facility("", {
      onSuccess: (data) => {
        hideLoader();
        setFacilityData(data.data);
      },
      onError: (error) => {},
    });
  }
  const { mutate: getFacilities } = useMutation(getFacilitiesData);
  function facilitiesDataFetch(selectedId) {
    showLoader();
    getFacilities(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setFacilityData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  const { mutate: getDevices } = useMutation(getAllDevicesById);
  function devicesDataFetch(selectedId) {
    showLoader();
    getDevices(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setDevicesData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  const { mutate: getDevicesByOrg } = useMutation(getAllDevicesByOrgId);
  function devicesDataFetchOrg(selectedId) {
    showLoader();
    getDevicesByOrg(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setDevicesData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  const { mutate: getDevicesByFac } = useMutation(getAllDevicesByFacId);
  function devicesDataFetchFac(selectedId) {
    showLoader();
    getDevicesByFac(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setDevicesData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  function getAllDepartments() {
    // //debugger
    getAllDep("", {
      onSuccess: (data) => {
        // //debugger
        hideLoader();
        setDepartmentsData(data.data);
      },
      onError: (error) => {},
    });
  }
  const { mutate: getDepartments } = useMutation(getDepartmentsData);
  function departmentsDataFetch(selectedId) {
    showLoader();
    getDepartments(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setDepartmentsData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  const { mutate: getDepartmentsByOrg } = useMutation(getAllDepByOrgId);
  function departmentsDataByOrg(selectedId) {
    showLoader();
    getDepartmentsByOrg(selectedId, {
      onSuccess: (data) => {
        hideLoader();
        setDepartmentsData(data.data);
      },
      onError: (error) => {
        hideLoader();
      },
    });
  }
  const [uploadedImageUrl, setUploadedImageUrl] = useState(null);
  const logoUrl = localStorage.getItem('organizationLogo');
  const { mutate: uploadLogo } = useMutation(LogoUpload);
  function LogoPost(selectedId) {
    // debugger
    showLoader();
    uploadLogo(selectedId, {
      onSuccess: (data) => {
        // debugger
        hideLoader();
        setUploadedImageUrl(data);
        if (logoUrl && logoUrl !== 'undefined') {
          LogoPostUpdate(data); // Replace 'someData' with the actual data you want to pass
        }
        // console.log(data)
      },
      onError: (error) => {
        // debugger
        hideLoader();
      },
    });
  }
  const { mutate: uploadLogoPost } = useMutation(LogoUploadPatch);
  function LogoPostUpdate(selectedId) {
    // debugger
    showLoader();
    uploadLogoPost(selectedId, {
      onSuccess: (data) => {
        // debugger
        hideLoader();
        setUploadedImageUrl(data.data.logo);
        console.log(data)
      },
      onError: (error) => {
        // debugger
        hideLoader();
      },
    });
  }
  useEffect(() => {
    // debugger
    // console.log(userPermissions)
    showLoader();
    if (organizationPermission) {
      organizationDataFetch();
      if (
        localStorage.getItem("OrganizationId") !== undefined ||
        localStorage.getItem("OrganizationId") !== null
      ) {
        facilitiesDataFetch(localStorage.getItem("OrganizationId"));
      }
      if (
        localStorage.getItem("facilityId") !== undefined ||
        localStorage.getItem("facilityId") !== null
      ) {
        departmentsDataFetch(localStorage.getItem("facilityId"));
      }
    } else if (facilityPermission) {
      getAllFacilities();
      if (
        localStorage.getItem("facilityId") !== undefined ||
        localStorage.getItem("facilityId") !== null
      ) {
        departmentsDataFetch(localStorage.getItem("facilityId"));
      }
    } else if (departmentPermission) {
      // // //debugger
      getAllDepartments();
    } else {
      hideLoader();
    }
  }, []);

  const { darkMode, setDarkMode } = useGlobalInfo();
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };
  const shouldShowOrganizationDropdown = () => {
    const pathsToHideOrganizationDropdown = ["/organizations"];
    return !pathsToHideOrganizationDropdown.includes(location.pathname);
  };
  const shouldShowFacilityDropdown = () => {
    const pathsToHideFacilityDropdown = [
      "/gateway",
      "/organization-sensors",
      "/Facilities",
      "/organizations",
      "/notification-alerts",
    ];
    return !pathsToHideFacilityDropdown.includes(location.pathname);
  };

  const shouldShowDepartmentDropdown = () => {
    const pathsToHideDepartmentDropdown = [
      "/gateway",
      "/organization-sensors",
      "/Facilities-staff",
      "/Facilities",
      "/department",
      "/organizations",
      "/notification-alerts",
    ];
    return !pathsToHideDepartmentDropdown.includes(location.pathname);
  };
  function saveHandler(handler) {
    debugger
    LogoPost(handler)
  }
  // function saveHandler(handler) {
  //   const logoUrl = localStorage.getItem('organizationLogo');
  
  //   if (logoUrl && logoUrl !== 'undefined') {
  //     // If logoUrl is defined and not 'undefined', call LogoPostUpdate
  //     LogoPostUpdate(handler);
  //   } else {
  //     // Otherwise, call LogoPost
  //     LogoPost(handler);
  //   }
  // }
  const [isModalOpen, setIsModalOpen] = useState(false);
  const closeModal = () => {
    setIsModalOpen(false);
  };
  const ModalOpen =()=>{
    setIsModalOpen(true)
  }
  return (
    
    <CHeader
      className={`mb-4 ${darkMode ? "header-dark" : ""}`}
      position="sticky"
    >
       <GenericModal
        title={"Settings"}
        content={
         <Settings  closeModal={closeModal} saveHandler={saveHandler} openModal={setIsModalOpen}   />
        }
        isOpen={isModalOpen}
        onClose={closeModal}
      />
      <CContainer fluid>
        <CHeaderToggler
          className="ps-1"
          onClick={() => dispatched({ type: "set", sidebarShow: !sidebarShow })}
        >
          <CIcon
            className={`  ${darkMode ? "icon-dark" : ""}`}
            icon={cilMenu}
            size="lg"
          />
        </CHeaderToggler>
        <CHeaderBrand className="ml-3 mr-4" to="/">
          <a href="#">
            <img
              src={Sitelogo}
              alt="Logo"
              style={{
                width: "105px",
                height: "62px",
                backgroundColor: "transparent",
              }}
            />
          </a>
        </CHeaderBrand>
        <CHeaderNav className="d-none d-md-flex me-auto gap-4">
          {organizationPermission && shouldShowOrganizationDropdown() && (
            <CNavItem>
              <select
                size="sm"
                className={` form-select ${darkMode ? "select-dark" : ""}`}
                aria-label="Organization"
                onChange={handleOrganizationChange}
                value={localStorage.getItem("OrganizationId")}
              >
                <option>Select Organization</option>
                {organizationData?.data?.map((item) => {
                  return (
                    <option key={item.customerid} value={item.customerid}>
                      {item.customername}
                    </option>
                  );
                })}
              </select>
            </CNavItem>
          )}
          {facilityPermission && shouldShowFacilityDropdown() && (
            <CNavItem>
              <select
                size="sm"
                className={` form-select ${darkMode ? "select-dark" : ""}`}
                aria-label="Facility"
                onChange={handleFacilityChange}
                value={localStorage.getItem("facilityId")}
              >
                <option>Select Facility</option>
                {facilityData?.map((item) => {
                  return (
                    <option key={item.facilityid} value={item.facilityid}>
                      {item.name}
                    </option>
                  );
                })}
              </select>
            </CNavItem>
          )}
          {departmentPermission && shouldShowDepartmentDropdown() && (
            <CNavItem>
              <select
                size="sm"
                className={` form-select ${darkMode ? "select-dark" : ""}`}
                aria-label="Department"
                onChange={handleDepartmentChange}
                value={localStorage.getItem("departmentId")}
              >
                <option>Select Department</option>
                {departmentsData?.map((item) => {
                  return (
                    <option key={item.departmentid} value={item.departmentid}>
                      {item.departmentname}
                    </option>
                  );
                })}
              </select>
            </CNavItem>
          )}
          <CNavItem>
            <CButton
              id={`${darkMode ? "button-dark" : ""}`}
              onClick={toggleDarkMode}
            >
              Change Mode
            </CButton>
          </CNavItem>
        </CHeaderNav>
        <CHeaderNav style={{ display: "block" }}>
          <h5
            className={`m-0 ${darkMode ? "heading-dark01" : "heading-light01"}`}
          >
            {role}
          </h5>
          <p
            className={`m-0 ${darkMode ? "heading-dark01" : "heading-light01"}`}
          >
            {mail}
          </p>
          {/* <CNavItem>
            <CNavLink href="#">
              <CIcon icon={cilEnvelopeOpen} size="lg" />
            </CNavLink>
          </CNavItem> */}
        </CHeaderNav>
        <CHeaderNav className="ms-3">
          <AppHeaderDropdown  closeModal={closeModal} ModalOpen={ModalOpen}   uploadedImageUrl={uploadedImageUrl}/>
        </CHeaderNav>
      </CContainer>
    </CHeader>
  );
};

export default AppHeader;
