import { setAuthenticationToken } from "./auth";
import { axiosInstance } from "./axios";
import {
  _department,
  _facility,
  _organization,
  _device,
  _view,
  _create,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";



const userPermissions = JSON.parse(localStorage.getItem("permissions"));
const organizationPermission = hasPermissionToActionAccess(
  userPermissions,
  _organization,
  _view
);
const facilityPermission = hasPermissionToActionAccess(
  userPermissions,
  _facility,
  _view
);
const departmentPermission = hasPermissionToActionAccess(
  userPermissions,
  _department,
  _view
);
const devicepermission = hasPermissionToActionAccess(
  userPermissions,
  _device,
  _view
);

export const getAllSensorData = async (id) => {
  //debugger
  setAuthenticationToken(localStorage.getItem("token"));
  const { data } = await axiosInstance.get(
    `sensor/get-assign-sensors-widget/${id}`
  );
  return data;
};

export const FindDashboard = async (payload) => {
  // //debugger
  setAuthenticationToken(localStorage.getItem("token"));
  const { data } = await axiosInstance.post(
    `dashboard/find`,
    payload
    //  {
    //   customerid: Number(payload),
    // }
  );
  return data;
};

