import React, { Suspense } from 'react'
import { Navigate, Route, Routes } from 'react-router-dom'
import * as constant from '../constants/permissionContant'
import { CContainer, CSpinner } from '@coreui/react'

// routes config
import routes from '../routes'
import { useLoader } from 'src/global-context/LoaderContext'
import GlobalLoader from './global-loader/GlobalLoader'
import { useGlobalInfo } from 'src/global-context/GlobalContext'
import CustomRoute from 'src/constants/CustomRoute'
import Dashboard from 'src/views/dashboard/Dashboard'
import Organization from 'src/views/pages/admin/organization/Organization'
import Facilities from 'src/views/pages/admin/facilities/Facilities'
import Department from 'src/views/pages/admin/department/Department'
import Devices from 'src/views/pages/admin/devices/Devices'
import FacilitiesStaff from 'src/views/pages/Staff/Facility/FacilitySatff'
import DepartmentStaff from 'src/views/pages/Staff/Department/DepartmentStaff'
import DevicesStaff from 'src/views/pages/Staff/Devices/DevicesStaff'
import Gateway from 'src/views/pages/admin/gateway/Gateway'
import NotificationSetup from 'src/views/pages/notification-setup/Notification-Setup'
import OrganizationSensor from 'src/views/pages/sensors/organization-sensors/OrganizationSensor'
import DevicesSensors from 'src/views/pages/sensors/devices-sensors/DevicesSensors'
import GraphPage from 'src/views/dashboard/Components/GraphPage'
const AppContent = () => {
  const { isLoading } = useLoader()
  // const { loading, setLoading } = useGlobalInfo()
  const { darkMode, setDarkMode } = useGlobalInfo()

  return (
    <>
      {isLoading ? (
        <GlobalLoader />
      ) : (
        <CContainer
          id="table_container"
          className={`table_container_light ${darkMode ? 'table_container_dark' : ''}`}
          style={{ maxWidth: 'unset' }}
          lg
        >
          <Suspense fallback={<CSpinner color="primary" />}>
            <Routes>
              {routes.map((route, idx) => {
                return (
                  route.element && (
                    <Route
                      key={idx}
                      path={route.path}
                      exact={route.exact}
                      name={route.name}
                      element={<route.element />}
                    />
                  )
                )
              })}
              <Route path="/" element={<Navigate to="dashboard" replace />} />
              <Route
                path="/dashboard"
                element={
                  <CustomRoute
                    component={Dashboard}
                    pageAccess={constant._device}
                    pageAction={[constant._view]}
                  />
                }
              />
              <Route
                path="/organizations"
                element={
                  <CustomRoute
                    component={Organization}
                    pageAccess={constant._organization}
                    pageAction={[constant._view]}
                  />
                }
              />
              <Route
                path="/department"
                element={
                  <CustomRoute
                    component={Department}
                    pageAccess={constant._department}
                    pageAction={[constant._view]}
                  />
                }
              />
              <Route
                path="/facilities"
                element={
                  <CustomRoute
                    component={Facilities}
                    pageAccess={constant._facility}
                    pageAction={[constant._view]}
                  />
                }
              />
                <Route
                path="/devices"
                element={
                  <CustomRoute
                    component={Devices}
                    pageAccess={constant._device}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/Facilities-staff"
                element={
                  <CustomRoute
                    component={FacilitiesStaff}
                    pageAccess={constant._facility}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/Devices-staff"
                element={
                  <CustomRoute
                    component={DevicesStaff}
                    pageAccess={constant._device}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/Department-staff"
                element={
                  <CustomRoute
                    component={DepartmentStaff}
                    pageAccess={constant._department}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/organization-sensors"
                element={
                  <CustomRoute
                    component={OrganizationSensor}
                    pageAccess={constant._department}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/devices-sensors"
                element={
                  <CustomRoute
                    component={DevicesSensors}
                    pageAccess={constant._sensor}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/gateway"
                element={
                  <CustomRoute
                    component={Gateway}
                    pageAccess={constant._organization}
                    pageAction={[constant._view]}
                  />
                }
              />
                <Route
                path="/notification-alerts"
                element={
                  <CustomRoute
                    component={NotificationSetup}
                    pageAccess={constant._organization}
                    pageAction={[constant._view]}
                  />
                }
              />
               <Route
                path="/sensor/graph"
                element={
                  <CustomRoute
                    component={GraphPage}

                    pageAccess={constant._sensor}
                    pageAction={[constant._view]}
                  />
                }
              />
            </Routes>
          </Suspense>
        </CContainer>
      )}
    </>
  )
}

export default React.memo(AppContent)
