import React from 'react';
import './BlueGraph.css';
import ReactApexChart from 'react-apexcharts';
import PropTypes from 'prop-types';
import Skeleton from 'react-loading-skeleton';

function BlueGraph({ sensorId, sensorsData, loading }) {
  const populateChart = () => {
    if (!sensorsData) {
      return {
        updatedOptions: {
          chart: {
            type: 'area',
            height: 350,
          },
          xaxis: {
            categories: [],
          },
        },
        updatedSeries: [],
      };
    }
  
    const { sensorName } = sensorsData;
  
    // Get the current date in the same format as your data
    const currentDate = new Date().toISOString().split('T')[0];
  
    // Extract timestamps and measures from the data
    const dataForCurrentDate = sensorsData[currentDate] || [];
  
    const timestamps = dataForCurrentDate.map((reading) => {
      const date = new Date(reading.reading_timestamp);
      return `${date.getHours()}:${(date.getMinutes() < 10 ? '0' : '') + date.getMinutes()}`;
    });
    const measures = dataForCurrentDate.map((reading) => parseFloat(reading.measure));
  
    // Update options with the extracted timestamps
    const updatedOptions = {
      chart: {
        type: 'area',
        height: 350,
      },
      xaxis: {
        categories: timestamps,
      },
    };
  
    // Update series with the extracted measures
    const updatedSeries = [
      {
        name: sensorName,
        data: measures,
      },
    ];
  
    return { updatedOptions, updatedSeries };
  };
  

  const { updatedOptions, updatedSeries } = populateChart();

  return (
    <div className="blue-top">
      <div className="main-container">
        {loading ? (
          <Skeleton active paragraph={{ rows: 3 }} />
        ) : (
          <>
            <div className="container">
              <p style={{color:'black'}}>{sensorsData?.sensorName}</p>
            </div>
            <div id="chart">
              <ReactApexChart
                options={updatedOptions}
                series={updatedSeries}
                type="area"
                height={350}
              />
            </div>
          </>
        )}
      </div>
    </div>
  );
}

BlueGraph.propTypes = {
  sensorId: PropTypes.string.isRequired,
  sensorsData: PropTypes.object.isRequired,
  loading: PropTypes.bool.isRequired,
};

export default BlueGraph;
