import React, { useEffect, useState } from "react";
import { useGlobalInfo } from "src/global-context/GlobalContext";
import {
  _department,
  _facility,
  _organization,
  _device,
  _view,
  _create,
  hasPermissionToActionAccess,
} from "src/constants/permissionContant";
import Charts from "../charts/Charts";
import { useMutation } from "react-query";
import {
  cilSortAscending,
  cilCheckAlt,
  cilWarning,
  cilAirplay,
  cilAlarm,
  cilToggleOff,
  cilLibraryBuilding,
  cilPeople,
  cilBuilding,
  cilDevices,
  cilAudio,
  cilAudioSpectrum,
} from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import Donut from "./Components/Donut";
import TableCard from "./Components/TableCard";
import GraphPage from "./Components/GraphPage";
import CounterComp from "./Counter_Comp";
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CCardTitle,
  CCardText,
  CRow,
  CCardHeader,
  CCardFooter,
  CHeader,
  CToast,
  CToastBody,
  CToastHeader,
  CToaster,
} from "@coreui/react";
import { FindDashboard } from "src/hooks/useSensors";
import Counter from "./Components/condition_cards/ConditionCards";
import { useLoader } from "src/global-context/LoaderContext";
import ConditionSummary from "./ConditionSummary/ConditionSummary";

import "./Dashboard.css";
const Dashboard = () => {
  const { dispatch } = useLoader();
  const { departmentId } = useGlobalInfo();
  const showLoader = () => dispatch({ type: "SHOW_LOADER" });
  const hideLoader = () => dispatch({ type: "HIDE_LOADER" });
  const { mutate: dashboardFind } = useMutation(FindDashboard);
  const userPermissions = JSON.parse(localStorage.getItem("permissions"));
  const organizationPermission = hasPermissionToActionAccess(
    userPermissions,
    _organization,
    _view
  );
  const facilityPermission = hasPermissionToActionAccess(
    userPermissions,
    _facility,
    _view
  );
  const departmentPermission = hasPermissionToActionAccess(
    userPermissions,
    _department,
    _view
  );
  const devicepermission = hasPermissionToActionAccess(
    userPermissions,
    _device,
    _view
  );
  const [organizationCount, setOrganiztionCount] = useState(0);
  const [facilityCount, setFacilityCount] = useState(0);
  const [departmentCount, setDepartmentCount] = useState(0);
  const [deviceCount, setDeviceCount] = useState(0);
  const [unAssignedSensorCount, setUnAssignedSensorCount] = useState(0);
  const [assignedSensorCount, setAssignedSensorCount] = useState(0);
  const orgID = localStorage.getItem("OrganizationId");
  const facID = localStorage.getItem("facilityId");
  const depID = localStorage.getItem("departmentId");

  const { darkMode, setDarkMode } = useGlobalInfo();

  function DashboardData(id, idType) {
    const payload = {
      [idType]: Number(id),
    };

    setTimeout(() => {
      dashboardFind(payload, {
        onSuccess: (res) => {
          setOrganiztionCount(res.data.counts?.OrganizationCount);
          setFacilityCount(res.data.counts?.FacilityCount);
          setDepartmentCount(res.data.counts?.DepartmentCount);
          setDeviceCount(res.data.counts?.DeviceCount);
          setUnAssignedSensorCount(res.data.counts?.UnAssignedSensorCount);
          setAssignedSensorCount(res.data.counts?.AssignedSensorCount);
          hideLoader();
        },
        onError: (error) => {
          setFacilityCount(0);
          setDepartmentCount(0);
          setDeviceCount(0);
          setUnAssignedSensorCount(0);
          setAssignedSensorCount(0);
          hideLoader();
        },
      });
    }, 0);
  }
  const [warnings, setwarnings] = useState(0);
  const [offline, setoffline] = useState(0);
  const [ok, setOk] = useState(0);
  const [Alarms, setAlarms] = useState(0);
  const [showwarnings, setShowwarnings] = useState(false);
  const [showoffline, setShowoffline] = useState(false);
  const [showok, setShowOk] = useState(false);
  const [showAlarms, setShowAlarms] = useState(false);
  const handleOkClick = () => {
    setShowOk(true);
    setShowoffline(false);
    setShowwarnings(false);
  };
  const handleOfflineClick = () => {
    setShowoffline(true);
    setShowwarnings(false);
    setShowOk(false);
  };
  const handleWarningsClick = () => {
    setShowwarnings(true);
    setShowoffline(false);
    setShowOk(false);
  };
  const handleClearClick = () => {
    setShowOk(false);
    setShowoffline(false);
    setShowwarnings(false);
  };
  // useEffect(() => {
  //   if (
  //     orgID !== null &&
  //     orgID !== undefined &&
  //     orgID !== "Select Organization"
  //   ) {
  //     DashboardData(orgID, "customerid");
  //   } else {
  //     DashboardData();
  //   }
  // }, [orgID, facID]);
  useEffect(() => {
    let filterValue, filterType;
    if (
      depID !== null &&
      depID !== undefined &&
      depID !== "Select Department"
    ) {
      filterValue = depID;
      filterType = "departmentid";
    } else if (
      facID !== null &&
      facID !== undefined &&
      facID !== "Select Facility"
    ) {
      filterValue = facID;
      filterType = "facilityid";
    } else if (orgID !== null && orgID !== undefined) {
      if (orgID !== "Select Organization") {
        filterValue = orgID;
        filterType = "customerid";
      } else {
        // orgID is "Select Organization," call DashboardData() without filters
        DashboardData();
        return;
      }
    }

    if (filterValue !== undefined && filterType !== undefined) {
      DashboardData(filterValue, filterType);
    } else {
      DashboardData();
    }
  }, [depID, facID, orgID]);
  return (
    <>
      {/* <Charts/> */}
      {organizationPermission ? (
        (localStorage.getItem("OrganizationId") &&
          localStorage.getItem("OrganizationId") !== "Select Organization" &&
          localStorage.getItem("OrganizationId") !== undefined) ||
        (localStorage.getItem("facilityId") &&
          localStorage.getItem("facilityId") !== "Select Facility") ||
        (localStorage.getItem("departmentId") &&
          localStorage.getItem("departmentId") !== "Select Department") ? (
          <>
            <div>
              <h1 id={`${darkMode ? "heading-dark" : ""}`}>
                Conditions Summary
              </h1>
              <h6 id={`${darkMode ? "heading-dark" : ""}`}>
                Keep track of your Sensor status
              </h6>
            </div>

            <div
              style={{
                marginTop: "30px",
                marginBottom: "30px",
                color: "black",
              }}
            >
              <h4 id={`${darkMode ? "heading-dark" : ""}`}>Overview</h4>
              <button
                style={{ borderRadius: "5px", padding: "5px" }}
                onClick={handleClearClick}
              >
                Clear Filter
              </button>
            </div>

            <div>
              <CRow className="status_card_container">
                {/* card red */}
                <CCol
                  md={6}
                  lg={3}
                  className="status_card"
                  id="status_card_red"
                  onClick={handleWarningsClick}
                >
                  <CIcon
                    icon={cilWarning}
                    alt="Name"
                    size="xl"
                    style={{ marginLeft: "10px" }}
                  />
                  <CCardText style={{ color: "#FF0505" }}>
                    <h1 style={{ fontSize: "60px", color: "#FF0505" }}>
                      {warnings}
                    </h1>
                    <p>Warning</p>
                  </CCardText>
                </CCol>
                {/* card yellow */}
                <CCol
                  sm={6}
                  md={6}
                  lg={3}
                  className="status_card"
                  id="status_card_yellow"
                  // onClick={setShowAlarms(true)}
                >
                  <CIcon
                    icon={cilAlarm}
                    alt="Name"
                    size="xl"
                    style={{ marginLeft: "10px" }}
                  />
                  <CCardText style={{ color: "#DA582C" }}>
                    <h1 style={{ fontSize: "60px", color: "#DA582C" }}>
                      {Alarms}
                    </h1>
                    <p>Alarm</p>
                  </CCardText>
                </CCol>
                {/* card green */}
                <CCol
                  sm={6}
                  md={6}
                  lg={3}
                  className="status_card"
                  id="status_card_green"
                  onClick={handleOkClick}
                >
                  <CIcon
                    icon={cilCheckAlt}
                    alt="Name"
                    size="xl"
                    style={{ marginLeft: "10px" }}
                  />
                  <CCardText style={{ color: "#32712F" }}>
                    <h1 style={{ fontSize: "60px", color: "#32712F" }}>{ok}</h1>
                    <p>Ok</p>
                  </CCardText>
                </CCol>
                {/* card blue */}
                <CCol
                  sm={6}
                  md={6}
                  lg={3}
                  className="status_card"
                  id="status_card_blue"
                  onClick={handleOfflineClick}
                >
                  <CIcon
                    icon={cilSortAscending}
                    alt="Name"
                    size="xl"
                    style={{ marginLeft: "10px" }}
                  />
                  <CCardText style={{ color: "#349FC4" }}>
                    <h1 style={{ fontSize: "60px", color: "#349FC4" }}>
                      {offline}
                    </h1>
                    <p>Offine</p>
                  </CCardText>
                </CCol>
                <CRow>
                  <Donut
                    setAlarms={setAlarms}
                    setoffline={setoffline}
                    setOk={setOk}
                    setwarnings={setwarnings}
                    showwarnings={showwarnings}
                    showoffline={showoffline}
                    showok={showok}
                    showAlarms={showAlarms}
                  />
                </CRow>
              </CRow>

              <>
                {/* <CRow> */}
                {/* </CRow> */}

                <TableCard />
              </>
            </div>
          </>
        ) : // </CCard>

        null
      ) : (
        // <>
        //   <ConditionSummary />
        // </>
        <>
          <div>
            <h1 id={`${darkMode ? "heading-dark" : ""}`}>Conditions summary</h1>
            <h6 id={`${darkMode ? "heading-dark" : ""}`}>
              Keep track of your Sensor status
            </h6>
          </div>

          <div
            style={{ marginTop: "30px", marginBottom: "30px", color: "black" }}
          >
            <h4 id={`${darkMode ? "heading-dark" : ""}`}>Overview</h4>
          </div>
          <CRow className="status_card_container">
            {/* card red */}
            <CCol md={6} lg={3} className="status_card" id="status_card_red">
              <CIcon
                icon={cilWarning}
                alt="Name"
                size="xl"
                style={{ marginLeft: "10px" }}
              />
              <CCardText style={{ color: "#FF0505" }}>
                <h1 style={{ fontSize: "60px", color: "#FF0505" }}>
                  {warnings}
                </h1>
                <p>Warning</p>
              </CCardText>
            </CCol>
            {/* card yellow */}
            <CCol
              sm={6}
              md={6}
              lg={3}
              className="status_card"
              id="status_card_yellow"
            >
              <CIcon
                icon={cilAlarm}
                alt="Name"
                size="xl"
                style={{ marginLeft: "10px" }}
              />
              <CCardText style={{ color: "#DA582C" }}>
                <h1 style={{ fontSize: "60px", color: "#DA582C" }}>{Alarms}</h1>
                <p>Alram</p>
              </CCardText>
            </CCol>
            {/* card green */}
            <CCol
              sm={6}
              md={6}
              lg={3}
              className="status_card"
              id="status_card_green"
            >
              <CIcon
                icon={cilCheckAlt}
                alt="Name"
                size="xl"
                style={{ marginLeft: "10px" }}
              />
              <CCardText style={{ color: "#32712F" }}>
                <h1 style={{ fontSize: "60px", color: "#32712F" }}>{ok}</h1>
                <p>Ok</p>
              </CCardText>
            </CCol>
            {/* card blue */}
            <CCol
              sm={6}
              md={6}
              lg={3}
              className="status_card"
              id="status_card_blue"
            >
              <CIcon
                icon={cilSortAscending}
                alt="Name"
                size="xl"
                style={{ marginLeft: "10px" }}
              />
              <CCardText style={{ color: "#349FC4" }}>
                <h1 style={{ fontSize: "60px", color: "#349FC4" }}>
                  {offline}
                </h1>
                <p>Offine</p>
              </CCardText>
            </CCol>
            <CRow>
              <Donut
                setAlarms={setAlarms}
                setoffline={setoffline}
                setOk={setOk}
                setwarnings={setwarnings}
                showwarnings={showwarnings}
                showoffline={showoffline}
                showok={showok}
                showAlarms={showAlarms}
              />
            </CRow>
          </CRow>
          <div>
            <>
              {/* <CRow> */}
              {/* </CRow> */}

              <TableCard />
            </>
          </div>
        </>
      )}
      {/* <CCard> */}
      <div
        style={{
          color: "black",
          marginTop: "20px",
        }}
      >
        <CRow
          className="my-3"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-around",
            flexDirection: "column",
            flexWrap: "wrap",
          }}
        >
          <div className="projectFactsWrap">
            {organizationPermission &&
              (!orgID || orgID === "Select Organization") && (
                <CounterComp
                  name="Organizations"
                  icon={cilLibraryBuilding}
                  count={organizationCount}
                />
              )}
            {facilityPermission && (!facID || facID === "Select Facility") && (
              <CounterComp
                name="Facilities"
                icon={cilPeople}
                count={facilityCount}
              />
            )}
            <CounterComp
              name="Devices "
              icon={cilDevices}
              count={deviceCount}
            />
            {departmentPermission &&
              (!depID || depID === "Select Department") && (
                <CounterComp
                  name="Departments "
                  icon={cilBuilding}
                  count={departmentCount}
                />
              )}
            {/* {organizationPermission &&
              (!orgID || orgID === "Select Organization") && ( */}
            <CounterComp
              name="Assigned Sensors "
              icon={cilAudio}
              count={assignedSensorCount}
            />
            {/* )} */}
            {organizationPermission &&
              (!orgID || orgID === "Select Organization") && (
                <CounterComp
                  name="UnAssigned Sensors "
                  icon={cilAudioSpectrum}
                  count={unAssignedSensorCount}
                />
              )}
          </div>
        </CRow>
        {/* <CRow>
        <Counter icon="fa-rocket" value="29" title="Web Development" isBlue />
        </CRow> */}
      </div>
      {/* </CCard> */}

      {/* {organizationPermission &&
              (!orgID || orgID === "Select Organization") && ( */}
      {/* <CounterComp name="Assigned Sensors " count={assignedSensorCount} /> */}
      {/* )} */}
      {/* {organizationPermission &&
              (!orgID || orgID === "Select Organization") && (
                <CounterComp
                  name="UnAssigned Sensors "
                  count={unAssignedSensorCount}
                />
              )}
          </div>
        </CRow> */}
      {/* <CRow>
        <Counter icon="fa-rocket" value="29" title="Web Development" isBlue />
        </CRow> */}
      {/* </div>p */}
    </>
  );
};

export default Dashboard;
