import React from "react";
import { useState, useEffect } from "react";
import "./ReportTable.css";
import { useGlobalInfo } from "src/global-context/GlobalContext";

const ReportTable = ({ date, day, data }) => {
  const { darkMode, setDarkMode } = useGlobalInfo();
  const [sensorData, setSensorData] = useState(data || []);
  // const [intervals,setIntervals]=useState(data.intervals)

  useEffect(() => {
    // If data changes externally, update the state
    if (data) {
      setSensorData(data);
    }
  }, [data]);
  // Dummy data for sensors
  const dummyData = [
    {
      name: "Heater",
      dates: [
        {
          date: "2023-01-01",
          intervals: [
            { interval: "1:00", value: 25 },
            { interval: "2:00", value: 30 },
            { interval: "3:00", value: 25 },
            { interval: "4:00", value: 30 },
            { interval: "5:00", value: 25 },
            { interval: "6:00", value: 30 },
            // Add more intervals as needed
          ],
        },
        // Add more dates as needed
      ],
    },
    {
      name: "Freezer",
      dates: [
        {
          date: "2023-01-01",
          intervals: [
            { interval: "1:00", value: 20 },
            { interval: "2:00", value: 22 },
            { interval: "3:00", value: 20 },
            { interval: "4:00", value: 22 },
            { interval: "5:00", value: 20 },
            { interval: "6:00", value: 22 },
          ],
        },
      ],
    },
    {
      name: "temp",
      dates: [
        {
          date: "2023-01-01",
          intervals: [
            { interval: "1:00", value: 20 },
            { interval: "2:00", value: 35 },
            { interval: "3:00", value: 20 },
            { interval: "4:00", value: 35 },
            { interval: "5:00", value: 20 },
            { interval: "6:00", value: 35 },
          ],
        },
      ],
    },
  ];
  const uniqueDates = [...new Set(sensorData.map((sensor) => sensor.date))];
  const uniqueIntervals = [
    ...new Set(
      sensorData.flatMap((sensor) =>
        sensor.readings.map((reading) => reading.interval)
      )
    ),
  ];

  const measuresMap = new Map();

  sensorData.forEach((sensor) => {
    const awsId = sensor.aws_id;
    const sensorName = sensor.sensorValue;
    const key = `${awsId}-${sensorName}`;

    const measures = measuresMap.get(key) || {
      awsId,
      sensorName,
      measures: [],
    };

    uniqueIntervals.forEach((interval) => {
      const intervalMeasures = sensor.readings
        .filter((reading) => reading.interval === interval)
        .map((reading) => ({
          measure: reading.measure,
          interval: reading.interval,
          min: sensor.min, // Include min from the sensor object
          max: sensor.max, // Include max from the sensor object
        }));
      measures.measures.push(intervalMeasures);
    });

    measuresMap.set(key, measures);
  });

  return (
    <>
      <table className="w-100 hccp_report_table">
        <thead>
          <tr>
            <th id={`${darkMode ? "heading-dark" : ""}`}>Sensor Name</th>
            {uniqueDates.map((uniqueDate) => (
              <th
                id={`${darkMode ? "heading-dark" : ""}`}
                key={uniqueDate}
                colSpan={uniqueIntervals.length}
              >
                {uniqueDate}
              </th>
            ))}
          </tr>
          <tr>
            <th></th>
            {uniqueDates.map(() =>
              uniqueIntervals.map((interval) => (
                <th key={interval}>{`${interval}:00`}</th>
              ))
            )}
          </tr>
        </thead>
        <tbody>
          {[...measuresMap.values()].map(({ awsId, sensorName, measures }) => (
            <tr key={`${awsId}-${sensorName}`}>
              <td id={`${darkMode ? "heading-dark" : ""}`}>{sensorName}</td>
              {measures.flat().map((reading, index) => {
                // Extract the relevant data from the reading
                const { measure, interval, min, max } = reading;
                // console.log("Measures:", measures);

                // Handle the case where measure is 0
                if (parseFloat(measure) === 0) {
                  return (
                    <td
                      key={`${awsId}-${sensorName}-${index}`}
                      style={{
                        backgroundColor: "lightgrey", // Gray for 0 value
                        border: "3px solid #bababa",
                        padding: "5px",
                      }}
                    >
                      n/a
                    </td>
                  );
                }

                return (
                  <td
                    key={`${awsId}-${sensorName}-${index}`}
                    style={{
                      backgroundColor:
                        parseFloat(measure) > parseFloat(max) ||
                        parseFloat(measure) < parseFloat(min)
                          ? "#FF0000" // Red for values outside the min-max range
                          : "#90EE90", // Green for normal values
                      border: "3px solid #bababa",
                      padding: "5px",
                    }}
                  >
                    {measure}
                  </td>
                );
              })}
            </tr>
          ))}
        </tbody>
        ;
      </table>
    </>
  );
};

export default ReportTable;
